import react from 'react';
import {Container} from 'bloomer';


export default()=>{
    const footerStyle ={
        // fontSize: '4rem',
        lineHeight: '1.6',
        color: '#222',
        backgroundColor: 'black'

    }
    return(<div style={footerStyle} className ="myFooter">
        <Container>
        <p>Built with React, Solidity, Web3, express, Bulma and some Magic By Rubin Njagi</p></Container>
        </div>)
}


